import React, { useState } from 'react';

function Admin_panel() {
  const [commision_value, setCommision_value] = useState('12312312312312321');
  const [id, setId] = useState('64029f0ba5335866a9a14e2e');

  const handleSubmit = async (event) => {
    event.preventDefault();

    try {
      const response = await fetch(`https://www.cyberial.app/admin/params/${id}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ commision_value })
      });

      if (!response.ok) {
        throw new Error('Ошибка обновления параметров');
      }

      console.log('Параметры обновлены');
    } catch (error) {
      console.error(error);
    }
  };

  return (
    <form style={{ zIndex: '99999' }} onSubmit={handleSubmit}>
      <label>
        ID:
        <input type="text" value={id} onChange={(event) => setId(event.target.value)} />
      </label>
      <br />
      <label>
        Param 1:
        <input type="text" value={commision_value} onChange={(event) => setCommision_value(event.target.value)} />
      </label>
      <br />
      {/* <label>
        Param 2:
        <input type="text" value={param2} onChange={(event) => setParam2(event.target.value)} />
      </label> */}
      <br />
      <button type="submit">Обновить параметры</button>
    </form>
  );
}



export default Admin_panel